.svg {
  background: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#graticule {
  fill: none;
  stroke: #dcdcdc;
  stroke-width: 0.5px;
  stroke-opacity: 0.5;
}

#sphere,
.arc {
  fill: none;
  stroke: #dcdcdc;
  stroke-width: 1px;
  stroke-opacity: 1;
}

.aux2 {
  stroke: #ff0000;
  stroke-width: 5px;
}

.aux2Left {
  stroke: #008000;
  stroke-width: 5px;
}

.aux2Right {
  stroke: #0000ff;
  stroke-width: 5px;
}
.p {
  stroke: #000000;
  stroke-width: 5px;
}



