App {
  height: 100%;
}
.container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 100%;
  height: 100%;
  text-align: center;
  background: black;
  color: WhiteSmoke;
  padding: 30px;
}

h1 {
  width: 100%;
  margin-bottom: 0;
}

h3 {
  width: 100%;
  margin-top: 0;
  color: darkgray;
}

.controls {
  width: 100%;
}

.visualizations {
  width: 100%;
}

@media screen and (min-width: 960px) {
  .container {
    margin-left: auto;
    margin-right: auto;
    width: 960px;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



